<template>
<TabContent style="height: 100%">
<v-container fill-height>
<v-row justify="center" align="center" class="text-center">
  <v-container style="max-width: 400px">
    <v-card>
  <v-card-title>Калькулятор возвратов</v-card-title>
  <v-card-text>
    <v-row>
      <v-col cols="12" md="6">
        <v-text-field v-model="endDate" label="Дата окончания"/>
      </v-col>
      <v-col cols="12" md="6">
        <v-text-field v-model="price" label="Стоимость в месяц"/>
      </v-col>
    </v-row>
    <v-text-field v-model="result" label="Результат" outline solo elevation="5"/>
    <div>День стоит: {{dayPrice}}₽</div>
    <div>Дней осталось: {{days}}</div>
    </v-card-text>
    </v-card>
  </v-container>
</v-row>
</v-container>
</TabContent>
</template>

<script>
import TabContent from '@/render/components/Common/TabContent.vue'

export default {
  components: { TabContent },
  props: ['value'],
  data: () => ({
    endDate: '2021-12-12',
    price: 100
  }),

  mounted () {
    const newDate = new Date(Date.now() - new Date().getTimezoneOffset() + 3600e3 * 3 + 86400e3)
    this.endDate = this.value.query.endDate || `${newDate.getFullYear()}-${newDate.getMonth() + 1}-${newDate.getDate()}`
    this.price = this.value.query.price || 100
    this.value.title = 'RefundCalculator'
    this.value.color = 'brown'
  },

  activated () {
  },

  computed: {
    dayPrice () {
      return Math.floor(this.price / 31 * 100) / 100
    },

    days () {
      const date = Date.now()
      const endTimestamp = Math.ceil(Math.abs(Date.parse(this.endDate)))
      return Math.floor((endTimestamp - date) / 24 / 60 / 60 / 1000)
    },

    result () {
      const resultPrice = this.dayPrice * this.days
      return Number.isNaN(resultPrice) ? '' : Math.floor(resultPrice * 100) / 100
    }
  },

  watch: {
    result (result) {
      this.value.fullTitle = `${this.endDate} / ${this.price}₽ = ${result}₽`
    }
  }
}
</script>
